import { useState } from "react";
import "./loader.css";
import { useTranslation } from "react-i18next";
import DCCircle from "../../Components/ChargingCircles/DC";
import ACCircle from "../../Components/ChargingCircles/AC";
import NoChargingCircle from "../../Components/ChargingCircles/NoCharging";
import { getTimeDifference } from "../../Utils/getTimeDifference";

import { toast } from "react-toastify";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import React, { useEffect } from 'react';

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
  FunctionsSettingsString: string | null;
}

const Charging: React.FC<Props> = ({ socket, jsonString ,FunctionsSettingsString}) => {
  const { t } = useTranslation();
  const [chargeType, setChargeType] = useState<"AC" | "DC">("AC");
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setnewjsonString] = useState(jsonString);
  const [newFunctionsSettingsString,setFunctionsSettingsString] = useState(FunctionsSettingsString);

  const [isCharging, setcharge] = useState(false);
  const [start_date, setstart_date] = useState("-");
  const [duration, setduration] = useState("-");
  const [current_L1, setcurrent_L1] = useState("0");
  const [current_L2, setcurrent_L2] = useState("0");
  const [current_L3, setcurrent_L3] = useState("0");
  const [voltage_L1, setvoltage_L1] = useState("0");
  const [voltage_L2, setvoltage_L2] = useState("0");
  const [voltage_L3, setvoltage_L3] = useState("0");
  const [power, setpower] = useState("0");
  const [energy, setenergy] = useState("0");
  const [card_type,setcard_type] = useState("");

  useEffect(() => {
    setSocket(socket)
    setnewjsonString(jsonString)
    setFunctionsSettingsString(FunctionsSettingsString)
    console.log("Charging...", jsonString)
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["charge"] != null)
        setcharge(jsonData.Data["charge"])
      if (jsonData.Data["start_date"] != null)
        setstart_date(jsonData.Data["start_date"])
      if (jsonData.Data["duration"] != null)
        setduration(jsonData.Data["duration"])
      if (jsonData.Data["current_L1"] != null)
        setcurrent_L1(jsonData.Data["current_L1"])
      if (jsonData.Data["current_L2"] != null)
        setcurrent_L2(jsonData.Data["current_L2"])
      if (jsonData.Data["current_L3"] != null)
        setcurrent_L3(jsonData.Data["current_L3"])
      if (jsonData.Data["voltage_L1"] != null)
        setvoltage_L1(jsonData.Data["voltage_L1"])
      if (jsonData.Data["voltage_L2"] != null)
        setvoltage_L2(jsonData.Data["voltage_L2"])
      if (jsonData.Data["voltage_L3"] != null)
        setvoltage_L3(jsonData.Data["voltage_L3"])
      if (jsonData.Data["power"] != null)
        setpower(jsonData.Data["power"])
      if (jsonData.Data["energy"] != null)
        setenergy(jsonData.Data["energy"])
    }
    if(FunctionsSettingsString){
      const jsonData = JSON.parse(FunctionsSettingsString);
      if (jsonData.Data["card_type"] != null){
        console.log("heeey",jsonData.Data["card_type"])
        setcard_type(jsonData.Data["card_type"])
      }
    }

  }, [socket,jsonString,FunctionsSettingsString]);

  async function StopCharge() {
    if (socket) {
      if (socket.readyState == socket.OPEN) {
        socket.send(JSON.stringify(
          {
            "Command": "StopTransaction",
            "Data": {}
          }
        ))
        setcharge(false)
      }
    }
  }

  async function StartCharge() {
    if (socket) {
      if (socket.readyState == socket.OPEN) {
        socket.send(JSON.stringify(
          {
            "Command": "StartTransaction",
            "Data": {}
          }
        ))
        setcharge(true)
      }
    }
  }

  return (
    <>
      <div className="container-fluid">
        <div
          id="ccMain"
          className="d-flex align-items-center justify-content-center"
          style={{ marginTop: "12vh", height: "300px" }}
        >
          {!isCharging ? (
            <NoChargingCircle />
          ) : chargeType === "DC" ? (
            <DCCircle />
          ) : (
            <ACCircle energy={energy}/>
          )}
        </div>
        <div className="row mt-4">
          <div className="col-12">
            <div className="card shadow-lg">
              <div className="px-5 z-index-1 bg-cover">
                <h4 className="text-body opacity-9 text-center">
                  {t("charge-details")}
                </h4>
                <hr className="horizontal light mt-1 mb-3" />

                <div className="row d-flex justify-content-between mt-3">

                  {/* START DATE */}
                  <div className="col-lg-3 col-12 my-auto">
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h4 className="text-body opacity-9">{t("start-date")}</h4>
                        <h3
                          className="text-body"
                          style={{ fontSize: "20px", textAlign: "center" }}
                        >
                          {start_date}
                        </h3>
                      </div>

                    </div>

                    {/* CURRENT L1 */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >

                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("current-L1")}
                        </h6>
                        <h3 className="text-body">
                          {current_L1}
                          <small className="text-sm align-top">A</small>
                        </h3>
                      </div>
                    </div>

                    {/* CURRENT L2 */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("current-L2")}
                        </h6>
                        <h3 className="text-body">
                          {current_L2}
                          <small className="text-sm align-top">A</small>
                        </h3>
                      </div>
                    </div>

                    {/* CURRENT L3 */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >

                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("current-L3")}
                        </h6>
                        <h3 className="text-body">
                          {current_L3}
                          <small className="text-sm align-top">A</small>
                        </h3>
                      </div>
                    </div>


                    {/* ENERGY */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("energy")}
                        </h6>
                        <h3 className="text-body">
                          {energy}
                          <small className="text-sm align-top">kwh</small>
                        </h3>
                      </div>
                    </div>
                  </div>

                  { card_type=="StartStopCard" &&
                    <div className="col-lg-3 col-12 my-auto">
                    <div
                      className="card d-flex justify-content-center cursor-pointer"
                      style={{
                        width: "100%",
                        height: "50px",
                        fontSize: "20px",
                        textAlign: "center",
                        color: "white",
                        background: isCharging ? "#CF182C" : "#39A56F",
                      }}
                      onClick={() => {
                        isCharging ? StopCharge() : StartCharge();
                      }}
                    >
                      {isCharging ? t("stop") : t("start")}
                    </div>
                  </div>}

                  <div className="col-lg-3 col-12 my-auto ">


                    {/* DURATION */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h4 className="text-body opacity-9">{t("time")}</h4>
                        <h3
                          className="text-body"
                          style={{ fontSize: "20px", textAlign: "center" }}
                        >
                          {duration}
                        </h3>
                      </div>

                    </div>

                    {/* VOLTAGE L1 */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("voltage-L1")}
                        </h6>
                        <h3 className="text-body">
                          {voltage_L1}
                          <small className="text-sm align-top">V</small>
                        </h3>
                      </div>
                    </div>

                    {/* VOLTAGE L2 */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("voltage-L2")}
                        </h6>
                        <h3 className="text-body">
                          {voltage_L2}
                          <small className="text-sm align-top">V</small>
                        </h3>
                      </div>
                    </div>

                    {/* VOLTAGE L3 */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >
                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("voltage-L3")}
                        </h6>
                        <h3 className="text-body">
                          {voltage_L3}
                          <small className="text-sm align-top">V</small>
                        </h3>
                      </div>
                    </div>



                    {/* POWER */}
                    <div
                      id="mobile-column"
                      className="d-flex justify-content-center"
                      style={{ gap: "20px" }}
                    >

                      <div className="text-center">
                        <h6 className="mb-0 text-body opacity-7">
                          {t("power")}
                        </h6>
                        <h3 className="text-body">
                          {power}
                          <small className="text-sm align-top">kw</small>
                        </h3>
                      </div>
                    </div>

                  </div>


                </div>
              </div>
            </div>
            <div className="row mt-4 text-center">
              <p className="text-sm">**{t("how-to-charge")}</p>
            </div>
          </div>
        </div>
      </div>
      <style>{`
        @media (max-width: 1200px) {
          #mobile-column {
            flex-direction: column !important;
          }

          #ccMain {
            margin-top: 100px !important;
          }

          #chargingCicle {
            height: 250px !important;
            width: 250px !important;
          }
        }`}</style>
    </>
  );
};

export default Charging;
