import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import { useState, useEffect } from "react";
import React from 'react';

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const MidMeter: React.FC<Props> = ({ socket, jsonString }) => {
  console.log("mid socket", socket);
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  var [newMidMeterString, setMidMeterString] = useState(jsonString);
  const [mid_id, setMidId] = useState("");
  const [externalMid, setExternalMid] = useState<boolean>(false);
  const minVal = 2;
  const maxVal = 247;

  useEffect(() => {
    setMidMeterString(jsonString);
    console.log("mid", jsonString);
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["mid_id"] != null)
        setMidId(jsonData.Data["mid_id"]);
      if (jsonData.Data["externalMid"] != null)
        setExternalMid(jsonData.Data["externalMid"]);
    }
  }, [jsonString]);

  async function SaveMid() {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        const value = parseInt(mid_id, 10);
        if (!isNaN(value) && value >= minVal && value <= maxVal) {
          socket.send(JSON.stringify({
            "Command": "MidMeter",
            "Data": {
              "externalMid": externalMid,
              "mid_id": value
            }
          }));
          toast.success(t("save-successful"));
        } else {
          toast.error(t("save-failed"));
        }
      }
    } else {
      toast.error(t("save-failed"));
    }
  }

  function handleInputChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    if (value === "" || /^\d+$/.test(value)) {
      setMidId(value);
    }
  }

  function handleLabelClick() {
    setExternalMid(!externalMid);
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <div className="row mb-3">
              <div className="col-12">
                <input
                  checked={externalMid}
                  name="mid_meter"
                  type="checkbox"
                  onChange={(e) => setExternalMid(e.target.checked)}
                />
                <label onClick={handleLabelClick} style={{ cursor: 'pointer' }}>
                  {t("mid-meter")}
                </label>
                <div className="col-12">
                  <label>Modbus Slave Id</label>
                  <input
                    name="mid_id"
                    value={mid_id}
                    onChange={handleInputChange}
                    className="form-control"
                    type="text"
                    placeholder={t("Please enter a number between 2 and 247", { min: minVal, max: maxVal })}
                    style={{ marginBottom: '20px' }}
                  />
                  {mid_id && (parseInt(mid_id, 10) < minVal || parseInt(mid_id, 10) > maxVal) && (
                    <div className="text-danger">Ltfen 2 ile 247 arasnda bir say girin</div>
                  )}
                </div>
                <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0">
                  <button
                    className="btn btn-primary btn-sm mb-0"
                    type="button"
                    name="button"
                    onClick={SaveMid}
                  >
                    {t("submit")}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MidMeter;