import { useTranslation } from "react-i18next";
import { useState } from "react";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import React, { useEffect } from 'react';

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const DeviceStatus: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setnewjsonString] = useState(jsonString);

  const [linkStatus, setlinkStatus] = useState("");
  const [strenghtOf4G, setstrenghtOf4G] = useState("");
  const [networkCard, setnetworkCard] = useState("");
  const [stateOfOcpp, setstateOfOcpp] = useState("");

  useEffect(() => {
    setSocket(socket)
    setnewjsonString(jsonString)
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["linkStatus"] != null)
        setlinkStatus(jsonData.Data["linkStatus"])
      if (jsonData.Data["strenghtOf4G"] != null)
        setstrenghtOf4G(jsonData.Data["strenghtOf4G"])
      if (jsonData.Data["networkCard"] != null)
        setnetworkCard(jsonData.Data["networkCard"])
      if (jsonData.Data["stateOfOcpp"] != null)
        setstateOfOcpp(jsonData.Data["stateOfOcpp"])
    }
  }, [socket,jsonString]);

  return (
    <>
      <div
        className="card shadow-lg mx-4 card-profile-bottom"
        style={{ marginTop: "12rem" }}
      >
        <div className="card-body p-3">
          <div className="row gx-4">
            <div className="col-auto">
              <div className="avatar avatar-xl position-relative">
                <img src="../assets/img/charging_status.png" />
              </div>
            </div>
            <div className="col-auto my-auto">
              <div className="h-100">
                <h5 className="mb-1">{t("device-status")}</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid py-4">
        <div className="row mb-3">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="font-weight-bolder mb-4">
                  {t("network-state")}
                </h5>
                <div className="row g-3">
                  <div className="col-12 col-sm-6 col-md-4">
                    <div className="card">
                      <div className="card-body">
                        <h5 className="font-weight-bolder">
                          {t("link-status")}
                        </h5>
                        <div className="row">
                          <div className="col-12 col-sm-6">
                            <p
                            style={{
                              color: `${
                                linkStatus === "Online"
                                  ? "green"
                                  : "red"
                              }`,
                            }}
                            >
                              {t(linkStatus)}
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-12 col-sm-6 col-md-4">
                    <div className="card">
                      <div className="card-body">
                        <h5 className="font-weight-bolder">
                          {t("strength-of-4g")}
                        </h5>
                        <div className="row">
                          <div className="col-12 col-sm-6">
                            <p>{strenghtOf4G}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-12 col-sm-6 col-md-4">
                    <div className="card">
                      <div className="card-body">
                        <h5 className="font-weight-bolder">
                          {t("network-card")}
                        </h5>
                        <div className="row">
                          <div className="col-12 col-sm-6">
                            <p>{networkCard}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="row mb-3">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="font-weight-bolder mb-4">
                  {t("state-of-ocpp")}
                </h5>
                <div className="row">
                  <div className="col-12 col-sm-6 col-md-4">
                    <div className="card">
                      <div className="card-body">
                        <h5 className="font-weight-bolder">
                          {t("background-connection")}
                        </h5>
                        <div className="row">
                          <div className="col-12 col-sm-6">
                            <p
                            style={{
                              color: `${
                                stateOfOcpp === "Online"
                                  ? "green"
                                  : "red"
                              }`,
                            }}
                            >
                              {t(stateOfOcpp)}
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default DeviceStatus;
