import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import { useState, useEffect } from "react";
import React from 'react';

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const ACCurrent: React.FC<Props> = ({ socket, jsonString }) => {

  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  var [newNetworkPriorityString, setACCurrent] = useState(jsonString);
  const [maxcurrent, setMaxCurrent] = useState("");
  const minVal = 6;
  const maxVal = 32;

  useEffect(() => {
    setACCurrent(jsonString);
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["maxcurrent"] != null)
        setMaxCurrent(jsonData.Data["maxcurrent"]);
    }
  }, [jsonString]);

  async function SaveACCurrent() {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        const value = parseInt(maxcurrent, 10);
        if (!isNaN(value) && value >= minVal && value <= maxVal) {
          socket.send(JSON.stringify({
            "Command": "ACCurrent",
            "Data": {
              "maxcurrent": value
            }
          }));
          toast.success(t("save-successful"));
        } else {
          toast.error(t("save-failed"));
        }
      }
    } else {
      toast.error(t("save-failed"));
    }
  }

  function handleInputChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    if (value === "" || /^\d+$/.test(value)) {
      setMaxCurrent(value);
    }
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <div className="row mb-3">
              <div className="col-12">
                <label>{t("ac-current")}</label>
                <input
                  name="maxcurrent"
                  value={maxcurrent}
                  onChange={handleInputChange}
                  className="form-control"
                  type="text"
                  placeholder={t("Please enter a number between 6 and 32", { min: minVal, max: maxVal })}
                  style={{ marginBottom: '20px' }}
                />
                {maxcurrent && (parseInt(maxcurrent, 10) < minVal || parseInt(maxcurrent, 10) > maxVal) && (
                  <div className="text-danger">{t("Please enter a number between 6 and 32", { min: minVal, max: maxVal })}</div>
                )}
                <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0">
                  <button
                    className="btn btn-primary btn-sm mb-0"
                    type="button"
                    name="button"
                    onClick={SaveACCurrent}
                  >
                    {t("submit")}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ACCurrent;