const TimezoneSettings: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setTimezoneSettings] = useState(jsonString);
  const [timezone, setTimezone] = useState("");

  useEffect(() => {
    setTimezoneSettings(jsonString)
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);

      if (jsonData.Data["timezone"] != null)
      setTimezone(jsonData.Data["timezone"])
    }
  }, [jsonString]);

  async function SaveTimezone() {
    if (socket) {
      if (socket.readyState == socket.OPEN) {
        socket.send(JSON.stringify(
          {
            "Command": "TimeZoneSettings",
            "Data": {
              "timezone": timezone
            }
          }
        ))
        toast.success(t("save-successful"));
      } else {
        toast.error(t("save-failed"));
      }
    } else {
      toast.error(t("save-failed"));
    }
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("time-zone-settings")}</h5>
            <div className="row">
              <div className="col-12 col-sm-6">
                <label className="mt-4">{t("time-zone")}</label>
                <select
                  value={timezone}
                  onChange={(e) => setTimezone(e.target.value)}
                  className="form-control mb-2"
                >
                  {Zones.map((zone) => {
                    return (
                      <option key={zone.value} value={zone.value}>
                        {zone.label}
                      </option>
                    );
                  })}
                </select>
                <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0">
                  <button
                    className="btn btn-primary btn-sm mb-0"
                    type="button"
                    name="button"
                    onClick={SaveTimezone}
                  >
                    {t("submit")}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TimezoneSettings;
