import { toast } from "react-toastify";
import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import React from "react";

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const OCPPSettings: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [file, setFile] = useState<File | null>(null);
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setOcppSettings] = useState(jsonString);
  const [domainName, setDomainName] = useState("");
  const [port, setPort] = useState("");
  const [sslEnable, setSslEnable] = useState("");
  const [path, setPath] = useState("");
  const [chargePointId, setChargePointId] = useState("");
  const [authorizationKey, setAuthorizationKey] = useState("");
  const [certFileError, setCertFileError] = useState<string | null>(null);

  const [domainError, setDomainError] = useState<string | null>(null);
  const [portError, setPortError] = useState<string | null>(null);

  useEffect(() => {
    setOcppSettings(jsonString);
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["domainName"] != null)
        setDomainName(jsonData.Data["domainName"]);
      if (jsonData.Data["port"] != null)
        setPort(jsonData.Data["port"]);
      if (jsonData.Data["sslEnable"] != null)
        setSslEnable(jsonData.Data["sslEnable"]);
      if (jsonData.Data["path"] != null)
        setPath(jsonData.Data["path"]);
      if (jsonData.Data["chargePointId"] != null)
        setChargePointId(jsonData.Data["chargePointId"]);
      if (jsonData.Data["authorizationKey"] != null)
        setAuthorizationKey(jsonData.Data["authorizationKey"]);
    }
  }, [jsonString]);

  function isValidDomainOrIp(value: string): boolean {
    const domainPattern = /^(?!-)[A-Za-z0-9-]+(\.[A-Za-z0-9]+)*(\.[A-Za-z]{2,})$/;
    const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
    return domainPattern.test(value) || ipPattern.test(value);
  }

  function isValidPort(port: string): boolean {
    const portNumber = parseInt(port, 10);
    return portNumber >= 0 && portNumber <= 65535;
  }

  function convertFileToBase64(file: File): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        const result = reader.result as string; // Type assertion ile string olarak iliyoruz
        resolve(result.split(',')[1] || '');
      };
      reader.onerror = error => reject(error);
    });
  }

  async function SaveOcppSettings() {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        if (!isValidDomainOrIp(domainName)) {
          setDomainError(t("Invalid domain name or IP address"));
          toast.error(t("Invalid domain name or IP address"));
          return;
        }
        if (!isValidPort(port)) {
          setPortError(t("Port number must be between 0 and 65535"));
          toast.error(t("Port number must be between 0 and 65535"));
          return;
        }

        setDomainError(null);
        setPortError(null);

        let base64File = null;
        if (file) {
          base64File = await convertFileToBase64(file);
        }

        const data = {
          "Command": "OcppSettings",
          "Data": {
            "domainName": domainName,
            "port": port,
            "sslEnable": sslEnable,
            "path": path,
            "chargePointId": chargePointId,
            "authorizationKey": authorizationKey,
            "certificate": base64File, // Sertifika base64 formatnda buraya ekleniyor
            "certFileName": file ? file.name : null // Dosya ad da JSON'a ekleniyor
          }
        };

        socket.send(JSON.stringify(data));

        if (!file) {
          setCertFileError(t("certifcate-not-selected-will-delete"));
        }

        toast.success(t("save-successful"));
      } else {
        toast.error(t("save-failed"));
      }
    } else {
      toast.error(t("save-failed"));
    }
  }

  function handleDomainChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setDomainName(value);
    if (isValidDomainOrIp(value)) {
      setDomainError(null);
    } else {
      setDomainError(t("Invalid domain name or IP address"));
    }
  }

  function handlePortChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setPort(value);
    if (isValidPort(value)) {
      setPortError(null);
    } else {
      setPortError(t("Port number must be between 0 and 65535"));
    }
  }

  function handleFileChange(e: React.ChangeEvent<HTMLInputElement>) {
    const selectedFile = e.target.files ? e.target.files[0] : null;
    if (selectedFile) {
      const allowedExtensions = ['pem', 'cert', 'crt', 'cer', 'der', 'pfx', 'p12', 'key', 'p7b', 'p7c', 'jks', 'keystore', 'ssp', 'sst'];
      const fileExtension = selectedFile.name.split('.').pop()?.toLowerCase();

      if (fileExtension && allowedExtensions.includes(fileExtension)) {
        setFile(selectedFile);
        setCertFileError(null); // Hata mesajn temizle
      } else {
        setCertFileError(t("certificate-invalid"));
        toast.error(t("certificate-invalid"));
        setFile(null);
      }
    } else {
      setCertFileError(t("certifcate-not-selected-will-delete"));
    }
  }

  function handleDeleteCertificate() {
    setFile(null); // Sertifika inputunu temizle
    SaveOcppSettings(); // Mevcut ayarlarla formu submit et
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("ocpp-settings")}</h5>
            <div className="row">
              <div className="col-12 col-sm-6">
                <label>{t("ip-or-domain-name")}</label>
                <input
                  name="ip"
                  value={domainName}
                  onChange={handleDomainChange}
                  className={`form-control ${domainError ? 'is-invalid' : ''}`}
                  type="text"
                />
                {domainError && <div className="text-danger">{domainError}</div>}
              </div>
              <div className="col-12 col-sm-6 mt-3 mt-sm-0">
                <label>{t("Port")}</label>
                <input
                  name="ocpp_port"
                  value={port}
                  onChange={handlePortChange}
                  className={`form-control ${portError ? 'is-invalid' : ''}`}
                  type="text"
                />
                {portError && <div className="text-danger">{portError}</div>}
              </div>
            </div>
            <div className="row">
              <div className="col-6">
                <label className="mt-4">{t("ssl-enable")}</label>
                <select
                  name="ssl_enable"
                  value={sslEnable}
                  onChange={(e) => setSslEnable(e.target.value)}
                  className="form-control"
                >
                  <option value="Enable">{t("enable")}</option>
                  <option value="Disable">{t("disable")}</option>
                </select>
              </div>
              <div className="col-6">
                <label className="mt-4">{t("path")}</label>
                <input
                  name="path1"
                  value={path}
                  onChange={(e) => setPath(e.target.value)}
                  className="form-control"
                  type="text"
                />
              </div>
            </div>
            <div className="row mb-3">
              <div className="col-12">
                <label className="mt-4">{t("charge-point-id")}</label>
                <input
                  name="charge_point_id"
                  value={chargePointId}
                  onChange={(e) => setChargePointId(e.target.value)}
                  className="form-control"
                  type="text"
                />
              </div>
            </div>
            <div className="row mb-3">
              <div className="col-12">
                <label className="mt-4">{t("authorization-key")}</label>
                <input
                  name="authorization_key"
                  value={authorizationKey}
                  onChange={(e) => setAuthorizationKey(e.target.value)}
                  className="form-control"
                  type="text"
                />
              </div>
            </div>
            <div className="row">
            <label className="mt-4">{t("certificate-import")}</label>
            </div>
            <div className="row mb-3">
              <div className="col-9">
                <input
                  id="certfile"
                  className="form-control"
                  type="file"
                  accept=".pem,.cert,.crt,.cer,.der,.pfx,.p12,.key,.p7b,.p7c,.jks,.keystore,.ssp,.sst"
                  onChange={handleFileChange}
                />
                {certFileError && <div className="text-danger mt-2">{certFileError}</div>}
              </div>
              <div className="col-3">
                <button
                  className="btn btn-primary btn-"
                  type="button"
                  onClick={handleDeleteCertificate}
                >
                  {t("delete-certificate")}
                </button>
              </div>
            </div>
            <button
              className="btn btn-primary btn-sm"
              type="button"
              name="button"
              onClick={SaveOcppSettings}
            >
              {t("submit")}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OCPPSettings;
