import RouteList from './Routes';
import './assets/css/argon-dashboard.css';
import './assets/css/nucleo-icons.css';
import './assets/css/fa-main.css';
import './assets/css/fa-v4-font-face.css';
import './assets/css/fa-v4-shims.css';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './App.css';


import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import { useState } from "react";
import React, { useEffect } from 'react';
import axios from 'axios';


const App = () => {
  const { t } = useTranslation();
  const [socket, setSocket] = useState<W3CWebSocket | null>(null);
  const [networkjsonString, setNetworkPriority] = useState<string | null>(null);
  const [fourGSettingsString, setfourGSettingsString] = useState<string | null>(null);
  const [ethernetSettingsString, setethernetSettingsString] = useState<string | null>(null);
  const [DNSSettingsString, setDNSSettingsString] = useState<string | null>(null);
  const [WifiSettingsString, setWifiSettingsString] = useState<string | null>(null);
  const [OcppSettingsString, setOcppSettingsString] = useState<string | null>(null);
  const [FunctionsSettingsString, setFunctionsSettingsString] = useState<string | null>(null);
  const [TimeZoneSettingsString, setTimeZoneSettingsString] = useState<string | null>(null);
  const [BluetoothSettingsString, setBluetoothSettings] = useState<string | null>(null);
  const [GetVersionString, setGetVersionString] = useState<string | null>(null);
  const [ChargingString, setChargingString] = useState<string | null>(null);
  const [DeviceStatusString, setDeviceStatusString] = useState<string | null>(null);
  const [ACCurrentString, setACCurrentString] = useState<string | null>(null);
  const [MidMeterString, setMidMeterString] = useState<string | null>(null);
  const [userName, setuserName] = useState<string | null>(null);
  const [userNameString, setuserNameString] = useState<string | null>(null);
  
  const [websocketip, setwebsocketip] = useState<string>();

  useEffect(() => {

    const fetchData = async () => {
      try {
        const response = await axios.get('../websocket.json');
        setwebsocketip(response.data.ip)
        

        const newSocket = new W3CWebSocket('ws://' + window.location.hostname + ':8000');

        newSocket.onopen = () => {
          console.log('WebSocket balants kuruldu.');
          setSocket(newSocket);
          console.log("socket",socket)
          console.log("newSocket",newSocket)
        };

        newSocket.onmessage = (message) => {
          const jsonData = JSON.parse(message.data.toString());
          console.log(jsonData)
          switch (jsonData.Command) {
            case "NetworkPriority":
              setNetworkPriority(message.data.toString());
              break
            case "4GSettings":
              setfourGSettingsString(message.data.toString());
              break
            case "EthernetSettings":
              setethernetSettingsString(message.data.toString());
              break
            case "DNSSettings":
              setDNSSettingsString(message.data.toString());
              break;
            case "WifiSettings":
              setWifiSettingsString(message.data.toString());
              break;
            case "OcppSettings":
              setOcppSettingsString(message.data.toString());
              break;
            case "FunctionsEnable":
              setFunctionsSettingsString(message.data.toString());
              break;
            case "BluetoothSettings":
              setBluetoothSettings(message.data.toString());
              break;
            case "TimeZoneSettings":
              setTimeZoneSettingsString(message.data.toString());
              break;
            case "FirmwareVersion":
              setGetVersionString(message.data.toString());
              break;
            case "Charging":
              setChargingString(message.data.toString())
              break;
            case "DeviceStatus":
              setDeviceStatusString(message.data.toString())
              break;
            case "ACCurrent":
              setACCurrentString(message.data.toString())
              break;
            case "MidMeter":
              setMidMeterString(message.data.toString())
            }
        };

        newSocket.onerror = (error) => {
          console.error('WebSocket hatas:', error);
        };

        newSocket.onclose = () => {
          console.log('WebSocket balants kapatld.');
        };

        console.log("Socket setlendi")

        return () => {
          console.log('WebSocket balants kapatld.');
          newSocket.close();
        };
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
    
  }, []);


  return (
    <div className="App">
      <RouteList  
                socket={socket} 
                networkjsonString={networkjsonString}
                fourGSettingsString={fourGSettingsString}
                ethernetSettingsString={ethernetSettingsString}
                DNSSettingsString={DNSSettingsString}
                WifiSettingsString={WifiSettingsString}
                OcppSettingsString={OcppSettingsString}
                FunctionsSettingsString={FunctionsSettingsString}
                TimeZoneSettingsString={TimeZoneSettingsString}
                BluetoothSettingsString={BluetoothSettingsString}
                GetVersionString={GetVersionString}
                ChargingString = {ChargingString}
                DeviceStatusString = {DeviceStatusString}
                ACCurrentString = {ACCurrentString}
                MidMeterString = {MidMeterString}
                userNameString = {userNameString}
                />
      <ToastContainer
        position="top-right"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </div>
  );
}

export default App;

