import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import React, { useEffect } from 'react';
import { useState } from "react";

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const BluetoothSettings: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setBluetoothSettings] = useState(jsonString);
  const [bluetooth_enable, setbluetooth_enable] = useState("");
  const [pin, setpin] = useState("");
  const [bluetooth_name, setbluetooth_name] = useState("");

  useEffect(() => {
    setBluetoothSettings(jsonString)
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);

      if (jsonData.Data["bluetooth_enable"] != null)
        setbluetooth_enable(jsonData.Data["bluetooth_enable"])
      if (jsonData.Data["pin"] != null)
        setpin(jsonData.Data["pin"])
      if (jsonData.Data["bluetooth_name"] != null)
        setbluetooth_name(jsonData.Data["bluetooth_name"])
    }
  }, [jsonString]);

  async function SaveBluetoothSettings() {
    if (socket) {
      if (socket.readyState == socket.OPEN) {
        socket.send(JSON.stringify(
          {
            "Command": "BluetoothSettings",
            "Data": {
              "bluetooth_enable": bluetooth_enable,
              "pin": pin,
              "bluetooth_name": bluetooth_name
            }
          }
        ))
        toast.success(t("save-successful"));
      }
      else{
        toast.error(t("save-failed"));
      }
    } 
    else{
      toast.error(t("save-failed"));
    }
    
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("bluetooth")}</h5>
            <div className="row mb-3">
              <div className="col-12 col-sm-6">
                <label className="mt-4">{t("bluetooth-enable")}</label>
                <select
                  name="server_enable"
                  value={bluetooth_enable}
                  onChange={(e) => setbluetooth_enable(e.target.value)}
                  className="form-control"
                >
                  <option value="Disable">{t("disable")}</option>
                  <option value="Enable">{t("enable")}</option>
                </select>
              </div>
              <div className="col-12 col-sm-6">
                <label className="mt-4">{t("bluetooth-name")}</label>
                <input
                  name="server_name"
                  value={bluetooth_name}
                  onChange={(e) => setbluetooth_name(e.target.value)}
                  className="form-control"
                  type="text"
                />
              </div>
              {/* <div className="col-12 col-sm-6">
                <label className="mt-4">{t("pin")}</label>
                <input
                  disabled
                  name="server_pin"
                  value={pin}
                  onChange={(e) => setpin(e.target.value)}
                  className="form-control"
                  type="text"
                />
              </div> */}
            </div>
            <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0">
              <button
                className="btn btn-primary btn-sm mb-0"
                type="button"
                name="button"
                onClick={SaveBluetoothSettings}
              >
                {t("submit")}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BluetoothSettings;
