import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import React, { useEffect } from 'react';
import { useState } from "react";

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const FunctionsSettings: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setFunctionsSettings] = useState(jsonString);
  const [card_type, setcard_type] = useState("");
  const [whether_to_open_the_qr_code_process, setwhether_to_open_the_qr_code_process] = useState("");
  const [local_startup_whether_to_go_ocpp_background, setlocal_startup_whether_to_go_ocpp_background] = useState("");
  const [whether_to_transfer_private_data, setwhether_to_transfer_private_data] = useState("");

  useEffect(() => {
    setFunctionsSettings(jsonString)
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);

      if (jsonData.Data["card_type"] != null)
        setcard_type(jsonData.Data["card_type"])
      if (jsonData.Data["whether_to_open_the_qr_code_process"] != null)
        setwhether_to_open_the_qr_code_process(jsonData.Data["whether_to_open_the_qr_code_process"])
      if (jsonData.Data["local_startup_whether_to_go_ocpp_background"] != null)
        setlocal_startup_whether_to_go_ocpp_background(jsonData.Data["local_startup_whether_to_go_ocpp_background"])
      if (jsonData.Data["whether_to_transfer_private_data"] != null)
        setwhether_to_transfer_private_data(jsonData.Data["whether_to_transfer_private_data"])
    }
  }, [jsonString]);

  async function SaveFunctionsSettings() {
    if (socket) {
      if (socket.readyState == socket.OPEN) {
        socket.send(JSON.stringify(
          {
            "Command": "FunctionsEnable",
            "Data": {
              "card_type": card_type,
              "whether_to_open_the_qr_code_process": whether_to_open_the_qr_code_process,
              "local_startup_whether_to_go_ocpp_background": local_startup_whether_to_go_ocpp_background,
              "whether_to_transfer_private_data": whether_to_transfer_private_data
            }
          }
        ))
        toast.success(t("save-successful"));
      } else {
        toast.error(t("save-failed"));
      }
    } else{
      toast.error(t("save-failed"));
    }
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("functions-enable")}</h5>
            <div className="row">
              <div className="col-12">
                {/* <label className="mt-4">{t("card-type")}</label> */}
                <select
                  name="card_type"
                  value={card_type}
                  onChange={(e) => setcard_type(e.target.value)}
                  className="form-control"
                >
                  <option value="StartStopCard">{t("start-stop-card")}</option>
                  <option value="LocalPnC">{t("local-pnc")}</option>
                  <option value="BillingCard">{t("billing-card")}</option>
                </select>
              </div>
              {/* <div className="col-12 col-sm-6">
                <label className="mt-4">{t("local-startup-absolute")}</label>
                <select
                  name="local_startup"
                  disabled
                  value={local_startup_whether_to_go_ocpp_background}
                  onChange={(e) => setlocal_startup_whether_to_go_ocpp_background(e.target.value)}
                  className="form-control"
                >
                  <option value="no">{t("no")}</option>
                  <option value="yes">{t("yes")}</option>
                </select>
              </div>
              <div className="col-12 col-sm-6">
                <label className="mt-4">{t("qr-open-absolute")}</label>
                <select
                  name="qr_code_process"
                  disabled
                  value={whether_to_open_the_qr_code_process}
                  onChange={(e) => setwhether_to_open_the_qr_code_process(e.target.value)}
                  className="form-control"
                >
                  <option value="no">{t("no")}</option>
                  <option value="yes">{t("yes")}</option>
                </select>
              </div>
              <div className="col-12 col-sm-6">
                <label className="mt-4">{t("transfer-private-data")}</label>
                <select
                  disabled
                  name="transfer_private_data"
                  value={whether_to_transfer_private_data}
                  onChange={(e) => setwhether_to_transfer_private_data(e.target.value)}
                  className="form-control"
                >
                  <option value="no">{t("no")}</option>
                  <option value="yes">{t("yes")}</option>
                </select>
              </div> */}
            </div>
            <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0 mt-3">
              <button
                className="btn btn-primary btn-sm mb-0"
                type="button"
                name="button"
                onClick={SaveFunctionsSettings}
              >
                {t("submit")}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FunctionsSettings;
