import { Navigate, Route, Routes } from "react-router-dom";
import AccountLayout from "../Layouts/AccountLayout";
import Login from "../Pages/Login";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import DashboardLayout from "../Layouts/DashboardLayout";
import Dashboard from "../Pages/Dashboard";
import Hardware from "../Pages/Hardware";
import QuickSetup from "../Pages/QuickSetup";
import Software from "../Pages/Software";
import DeviceStatus from "../Pages/DeviceStatus";
import Uploads from "../Pages/Uploads";
import ErrorLogs from "../Pages/ErrorLogs";
import Profile from "../Pages/Profile";
import AuthProvider from "../Providers/AuthProvider";
import Charging from "../Pages/Charging";
import LoginProvider from "../Providers/LoginProvider";
import React, { useEffect } from 'react';
import { useTranslation } from "react-i18next";
import { useState } from "react";

interface Props {
  socket: W3CWebSocket | null;
  networkjsonString: string | null;
  fourGSettingsString: string | null;
  ethernetSettingsString: string | null;
  DNSSettingsString: string | null;
  WifiSettingsString: string | null;
  OcppSettingsString: string | null;
  FunctionsSettingsString: string | null;
  TimeZoneSettingsString: string | null;
  BluetoothSettingsString: string | null;
  GetVersionString: string | null;
  ChargingString: string | null;
  DeviceStatusString : string | null;
  ACCurrentString: string | null;
  MidMeterString: string | null;
  userNameString: string | null;
}

const RouteList: React.FC<Props> = ({ socket, networkjsonString, 
  fourGSettingsString, ethernetSettingsString, 
  DNSSettingsString, WifiSettingsString, 
  OcppSettingsString, FunctionsSettingsString, 
  TimeZoneSettingsString, BluetoothSettingsString, 
  GetVersionString, ChargingString, DeviceStatusString, ACCurrentString, MidMeterString, userNameString   }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newChargingString, setChargingString] = useState(ChargingString);
  const [newFunctionsSettingsString, setFunctionsSettingsString] = useState(FunctionsSettingsString);
  const [newDeviceStatusString, setDeviceStatusString] = useState(DeviceStatusString);
  const [newACCurrentString, setACCurrentString] = useState(ACCurrentString);
  const [newMidMeterString, setMidMeterString] = useState(MidMeterString);
  const [userName, setuserName] = useState(userNameString);

  useEffect(() => {
    setSocket(socket)
    setChargingString(ChargingString)
    setFunctionsSettingsString(FunctionsSettingsString)
    setDeviceStatusString(DeviceStatusString)
    setACCurrentString(ACCurrentString)
    setMidMeterString(MidMeterString)
  }, [ChargingString,FunctionsSettingsString,DeviceStatusString,ACCurrentString,MidMeterString]);


  return (
    <Routes>
      <Route path="/" element={<Navigate to="/account/login" replace />} />

      {/*<Route path="/" element={<Navigate to="admin" replace />} />*/}
      
      <Route path="" element={<LoginProvider />}>
        <Route path="account" element={<AccountLayout />}>
          <Route path="login" element={<Login setUserNameGeneral={setuserName}/>} />
        </Route>
      </Route>

      <Route path="" element={<AuthProvider />}>
        <Route path="admin" element={<DashboardLayout userName={userName}/>}>
          <Route path="dashboard" element={<Dashboard />} />
          {/* <Route path="quick-setup" element={<QuickSetup />} /> */}
          <Route path="software" element={<Software />} />
          <Route path="status" element={<DeviceStatus socket={newSocket} jsonString={newDeviceStatusString} />} />
          <Route path="profile" element={<Profile />} />
          {/* <Route path="uploads" element={<Uploads />} /> */}
          <Route path="charging" element={<Charging socket={newSocket} jsonString={newChargingString} FunctionsSettingsString={newFunctionsSettingsString} />} />
          <Route path="hardware" element={<Hardware socket={newSocket} ACCurrentString={newACCurrentString} MidMeterString= {newMidMeterString}/>} />
          {/* <Route path="error-logs" element={<ErrorLogs />} /> */}
        </Route>
      </Route>

      <Route path="*" element={<Navigate to="/404" replace />} />
    </Routes>
  );
};

export default RouteList;
