import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import React, { useEffect } from 'react';
import { useState } from "react";

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}



const GetVersion: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setGetVersion] = useState(jsonString);
  const [version, setversion] = useState("");

  useEffect(() => {
    setGetVersion(jsonString)
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);

      if (jsonData.Data["version"] != null)
      setversion(jsonData.Data["version"])
    }
  }, [jsonString]);

  async function SaveTimezone() {
    if (socket) {
      if (socket.readyState == socket.OPEN) {
        socket.send(JSON.stringify(
          {
            "Command": "FirmwareVersion",
            "Data": {
              "version": version
            }
          }
        ))
        toast.success(t("save-successful"));
      } else {
        toast.error(t("save-failed"));
      }
    } else {
      toast.error(t("save-failed"));
    }
  }

  return (
    <div className="row">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("get-version")}</h5>
            <div className="row mb-3">
              <div className="col-12">
                <label>{t("version")}</label>
                <input disabled 
                name="version" 
                value={version} 
                onChange={(e) => setversion(e.target.value)}
                className="form-control" 
                type="text" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GetVersion;
