import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import React, { useEffect, useState } from 'react';

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const WifiSettings: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setWifiSettings] = useState(jsonString);
  const [wifiEnable, setWifiEnable] = useState<boolean>(false);
  const [mod, setMod] = useState("");
  const [ssid, setSSID] = useState("");
  const [password, setPassword] = useState("");
  const [encryptionType, setEncryptionType] = useState("");
  const [wifidhcpcEnable, setDhcpcEnable] = useState<boolean>(false);
  const [ip, setIp] = useState("");
  const [netmask, setNetmask] = useState("");
  const [gateway, setGateway] = useState("");
  const [ssidError, setSsidError] = useState<string | null>(null);
  const [passwordError, setPasswordError] = useState<string | null>(null);
  const [ipError, setIpError] = useState<string | null>(null);
  const [netmaskError, setNetmaskError] = useState<string | null>(null);
  const [gatewayError, setGatewayError] = useState<string | null>(null);

  useEffect(() => {
    setWifiSettings(jsonString);
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["wifiEnable"] != null)
        setWifiEnable(jsonData.Data["wifiEnable"]);
      if (jsonData.Data["mod"] != null)
        setMod(jsonData.Data["mod"]);
      if (jsonData.Data["ssid"] != null)
        setSSID(jsonData.Data["ssid"]);
      if (jsonData.Data["password"] != null)
        setPassword(jsonData.Data["password"]);
      if (jsonData.Data["encryptionType"] != null)
        setEncryptionType(jsonData.Data["encryptionType"]);
      if (jsonData.Data["wifidhcpcEnable"] != null)
        setDhcpcEnable(jsonData.Data["wifidhcpcEnable"]);
      if (jsonData.Data["ip"] != null)
        setIp(jsonData.Data["ip"]);
      if (jsonData.Data["netmask"] != null)
        setNetmask(jsonData.Data["netmask"]);
      if (jsonData.Data["gateway"] != null)
        setGateway(jsonData.Data["gateway"]);
    }
  }, [jsonString]);

  async function SaveWifiSettings() {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        if (!isValidSsid(ssid)) {
          setSsidError(t("SSID length must be between 1 and 32 characters"));
          toast.error(t("SSID length must be between 1 and 32 characters"));
          return;
        }
        if (encryptionType !== "OPEN" && !isValidPassword(password)) {
          setPasswordError(t("Password length must be between 8 and 64 characters"));
          toast.error(t("Password length must be between 8 and 64 characters"));
          return;
        }
        if (!wifidhcpcEnable && (!isValidIp(ip) || !isValidIp(netmask) || !isValidIp(gateway))) {
          if (!isValidIp(ip)) {
            setIpError(t("Invalid IP"));
            toast.error(t("Invalid IP"));
          }
          if (!isValidIp(netmask)) {
            setNetmaskError(t("Invalid subnet mask"));
            toast.error(t("Invalid subnet mask"));
          }
          if (!isValidIp(gateway)) {
            setGatewayError(t("Invalid IP"));
            toast.error(t("Invalid IP"));
          }
          return;
        }
        setSsidError(null);
        setPasswordError(null);
        setIpError(null);
        setNetmaskError(null);
        setGatewayError(null);

        socket.send(JSON.stringify({
          "Command": "WifiSettings",
          "Data": {
            "wifiEnable": wifiEnable,
            "mod": mod,
            "ssid": ssid,
            "password": encryptionType !== "OPEN" ? password : "",
            "encryptionType": encryptionType,
            "wifidhcpcEnable": wifidhcpcEnable,
            "ip": ip,
            "netmask": netmask,
            "gateway": gateway
          }
        }));
        toast.success(t("save-successful"));
      } else {
        toast.error(t("save-failed"));
      }
    } else {
      toast.error(t("save-failed"));
    }
  }

  function handleSsidChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setSSID(value);
    if (isValidSsid(value)) {
      setSsidError(null);
    } else {
      setSsidError(t("SSID length must be between 1 and 32 characters"));
    }
  }

  function handlePasswordChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setPassword(value);
    if (isValidPassword(value)) {
      setPasswordError(null);
    } else {
      setPasswordError(t("Password length must be between 8 and 64 characters"));
    }
  }

  function handleIpChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setIp(value);
    if (isValidIp(value)) {
      setIpError(null);
    } else {
      setIpError(t("Invalid IP"));
    }
  }

  function handleNetmaskChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setNetmask(value);
    if (isValidIp(value)) {
      setNetmaskError(null);
    } else {
      setNetmaskError(t("Invalid subnet mask"));
    }
  }

  function handleGatewayChange(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setGateway(value);
    if (isValidIp(value)) {
      setGatewayError(null);
    } else {
      setGatewayError(t("Invalid IP"));
    }
  }

  function isValidSsid(value: string): boolean {
    return value.length > 0 && value.length <= 32;
  }

  function isValidPassword(value: string): boolean {
    return value.length >= 8 && value.length <= 64;
  }

  function isValidIp(value: string): boolean {
    const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
    return ipPattern.test(value);
  }

  function handleWifiLabelClick() {
    setWifiEnable(!wifiEnable);
  }

  function handleDhcpLabelClick() {
    setDhcpcEnable(!wifidhcpcEnable);
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("wifi-configuration")}</h5>
            <div className="row">
              <div className="col-12">
                <input
                  type="checkbox"
                  checked={wifiEnable}
                  onChange={(e) => setWifiEnable(e.target.checked)}
                />
                <label htmlFor="wifiEnable" onClick={handleWifiLabelClick} style={{ cursor: 'pointer' }}>
                  {t("wifi-enable")}
                </label>
                <div id="wifi-configuration-form">
                  <div className="row mb-3">
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("mode-selection")}</label>
                      <select
                        name="mode_selection"
                        disabled={!wifiEnable}
                        className="form-control"
                        value={mod}
                        onChange={(e) => setMod(e.target.value)}
                        id="mode_selection"
                      >
                        <option value="">{t("select")}</option>
                        <option value="STA">STA</option>
                        <option value="AP">AP</option>
                      </select>
                    </div>
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("ssid")}</label>
                      <input
                        name="ssid"
                        disabled={!wifiEnable}
                        value={ssid}
                        onChange={handleSsidChange}
                        className={`form-control ${ssidError ? 'is-invalid' : ''}`}
                        type="text"
                      />
                      {ssidError && (
                        <div className="text-danger">{ssidError}</div>
                      )}
                    </div>
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("password")}</label>
                      <input
                        disabled={!wifiEnable || encryptionType === "OPEN"}
                        name="wifi_password"
                        value={password}
                        onChange={handlePasswordChange}
                        className={`form-control ${passwordError ? 'is-invalid' : ''}`}
                        type="text"
                      />
                      {passwordError && (
                        <div className="text-danger">{passwordError}</div>
                      )}
                    </div>
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("encryption")}</label>
                      <select
                        name="encryption"
                        disabled={!wifiEnable}
                        value={encryptionType}
                        onChange={(e) => setEncryptionType(e.target.value)}
                        className="form-control"
                      >
                        <option value="OPEN">OPEN</option>
                        <option value="WEP">WEP</option>
                        <option value="WPA">WPA</option>
                        <option value="WPA2">WPA2</option>
                      </select>
                    </div>
                  </div>
                  <input
                    type="checkbox"
                    checked={wifidhcpcEnable}
                    onChange={(e) => setDhcpcEnable(e.target.checked)}
                  />
                  <label htmlFor="wifidhcpcEnable" onClick={handleDhcpLabelClick} style={{ cursor: 'pointer' }}>
                    {t("wifi-dhcpc-enable")}
                  </label>
                  <div className="row mb-3">
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("ip")}</label>
                      <input
                        name="ip"
                        value={ip}
                        disabled={!(wifiEnable && !wifidhcpcEnable)}
                        onChange={handleIpChange}
                        className={`form-control ${ipError ? 'is-invalid' : ''}`}
                        type="text"
                      />
                      {ipError && (
                        <div className="text-danger">{ipError}</div>
                      )}
                    </div>
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("netmask")}</label>
                      <input
                        value={netmask}
                        disabled={!(wifiEnable && !wifidhcpcEnable)}
                        onChange={handleNetmaskChange}
                        className={`form-control ${netmaskError ? 'is-invalid' : ''}`}
                        type="text"
                      />
                      {netmaskError && (
                        <div className="text-danger">{netmaskError}</div>
                      )}
                    </div>
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("gateway")}</label>
                      <input
                        name="gateway"
                        value={gateway}
                        disabled={!(wifiEnable && !wifidhcpcEnable)}
                        onChange={handleGatewayChange}
                        className={`form-control ${gatewayError ? 'is-invalid' : ''}`}
                        type="text"
                      />
                      {gatewayError && (
                        <div className="text-danger">{gatewayError}</div>
                      )}
                    </div>
                  </div>
                  <div className="row mb-3"></div>
                </div>
                <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0">
                  <button
                    className="btn btn-primary btn-sm mb-0"
                    type="button"
                    name="button"
                    onClick={SaveWifiSettings}
                  >
                    {t("submit")}
                  </button>
                </div>
              </div>
              <div className="col-12"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WifiSettings;