import ChargePointId from "../../Components/ChargePointId";
import GetVersion from "../../Components/GetVersion";
import Identification from "../../Components/Identification";
import ACCurrent from "../../Components/ACCurrent";
import MidMeter from "../../Components/MidMeter";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket"
import { useState } from "react";
import React, { useEffect } from 'react';

interface Props {
  socket: W3CWebSocket | null;
  ACCurrentString: string | null;
  MidMeterString: string | null;
}

const Hardware: React.FC<Props> = ({ socket, ACCurrentString, MidMeterString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  var [newACCurrentString, setACCurrentString] = useState(ACCurrentString);
  var [newMidMeterString, setMidMeterString] = useState(MidMeterString);

  useEffect(() => {
    setSocket(socket)
    setACCurrentString(ACCurrentString)
    setMidMeterString(MidMeterString)
  }, [ACCurrentString,MidMeterString,socket]);

  return (
    <>
      <div
        className="card shadow-lg mx-4 card-profile-bottom"
        style={{ marginTop: "12rem" }}
      >
        <div className="card-body p-3">
          <div className="row gx-4">
            <div className="col-auto">
              <div className="avatar avatar-xl position-relative">
                <img src="../assets/img/hardware_settings.png" />
              </div>
            </div>
            <div className="col-auto my-auto">
              <div className="h-100">
                <h5 className="mb-1">{t("hardware-settings")}</h5>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid py-4">
        <ACCurrent socket={newSocket} jsonString={newACCurrentString} />
        <MidMeter socket={newSocket} jsonString={newMidMeterString} />
      </div>
    </>
  );
};

export default Hardware;
