import { toast } from "react-toastify";
import { useTranslation } from "react-i18next";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import React, { useEffect, useState } from 'react';

interface Props {
  socket: W3CWebSocket | null;
  jsonString: string | null;
}

const DNSSettings: React.FC<Props> = ({ socket, jsonString }) => {
  const { t } = useTranslation();
  const [newSocket, setSocket] = useState(socket);
  const [newjsonString, setDNSSettings] = useState(jsonString);
  const [dnsEnable, setdnsEnable] = useState<boolean>(false);
  const [DNS1, setDNS1] = useState("");
  const [DNS2, setDNS2] = useState("");
  const [dns1Error, setDns1Error] = useState<string | null>(null);
  const [dns2Error, setDns2Error] = useState<string | null>(null);

  useEffect(() => {
    setDNSSettings(jsonString);
    if (jsonString) {
      const jsonData = JSON.parse(jsonString);
      if (jsonData.Data["dnsEnable"] != null)
        setdnsEnable(jsonData.Data["dnsEnable"]);
      if (jsonData.Data["DNS1"] != null)
        setDNS1(jsonData.Data["DNS1"]);
      if (jsonData.Data["DNS2"] != null)
        setDNS2(jsonData.Data["DNS2"]);
    }
  }, [jsonString]);

  async function SaveDNSSettings() {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        if (!isValidIp(DNS1) || !isValidIp(DNS2)) {
          setDns1Error(!isValidIp(DNS1) ? t("Invalid DNS address") : null);
          setDns2Error(!isValidIp(DNS2) ? t("Invalid DNS address") : null);
          toast.error(t("Invalid DNS address"));
          return;
        }
        setDns1Error(null);
        setDns2Error(null);
        socket.send(JSON.stringify({
          "Command": "DNSSettings",
          "Data": {
            "dnsEnable": dnsEnable,
            "DNS1": DNS1,
            "DNS2": DNS2
          }
        }));
        toast.success(t("save-successful"));
      } else {
        toast.error(t("save-failed"));
      }
    }
  }

  function handleDns1Change(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setDNS1(value);
    if (isValidIp(value)) {
      setDns1Error(null);
    } else {
      setDns1Error(t("Invalid DNS address"));
    }
  }

  function handleDns2Change(e: React.ChangeEvent<HTMLInputElement>) {
    const value = e.target.value;
    setDNS2(value);
    if (isValidIp(value)) {
      setDns2Error(null);
    } else {
      setDns2Error(t("Invalid DNS address"));
    }
  }

  function isValidIp(value: string): boolean {
    const ipPattern = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])(\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])){3}$/;
    return ipPattern.test(value);
  }

  function handleDnsLabelClick() {
    setdnsEnable(!dnsEnable);
  }

  return (
    <div className="row mb-3">
      <div className="col-12">
        <div className="card">
          <div className="card-body">
            <h5 className="font-weight-bolder">{t("dns-configuration")}</h5>
            <div className="row">
              <div className="col-12">
                <input
                  type="checkbox"
                  name="dns_enabled"
                  checked={dnsEnable}
                  onChange={(e) => setdnsEnable(e.target.checked)}
                />
                <label htmlFor="dns_enabled" onClick={handleDnsLabelClick} style={{ cursor: 'pointer' }}>
                  {t("dns-enable")}
                </label>
                <div id="dns-configuration-form">
                  <div className="row mb-3">
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("dns")} 1</label>
                      <input
                        name="wifi_dns1"
                        value={DNS1}
                        onChange={handleDns1Change}
                        className={`form-control ${dns1Error ? 'is-invalid' : ''}`}
                        type="text"
                        disabled={!dnsEnable}
                      />
                      {dns1Error && (
                        <div className="text-danger">{dns1Error}</div>
                      )}
                    </div>
                    <div className="col-12 col-md-6">
                      <label className="mt-4">{t("dns")} 2</label>
                      <input
                        name="wifi_dns2"
                        value={DNS2}
                        onChange={handleDns2Change}
                        className={`form-control ${dns2Error ? 'is-invalid' : ''}`}
                        type="text"
                        disabled={!dnsEnable}
                      />
                      {dns2Error && (
                        <div className="text-danger">{dns2Error}</div>
                      )}
                    </div>
                  </div>
                </div>
                <div className="d-flex justify-content-between justify-content-sm-start gap-sm-3 gap-0">
                  <button
                    className="btn btn-primary btn-sm mb-0"
                    type="button"
                    name="button"
                    onClick={SaveDNSSettings}
                  >
                    {t("submit")}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DNSSettings;